@font-face {
  font-family: "Avenir Next";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("/fonts/AvenirNext-Medium.woff2") format("woff2"), url("/fonts/AvenirNext-Medium.woff") format("woff");
}
@font-face {
  font-family: "Avenir Next";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("/fonts/AvenirNext-Demi.woff2") format("woff2"), url("/fonts/AvenirNext-Demi.woff") format("woff");
}
@font-face {
  font-family: "Avenir Next";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("/fonts/AvenirNext-Bold.woff2") format("woff2"), url("/fonts/AvenirNext-Bold.woff") format("woff");
}
@font-face {
  font-family: "Playfair Display";
  font-style: italic;
  font-weight: 700;
  font-display: swap;
  src: url("/fonts/PlayfairDisplay-BoldItalic.woff2") format("woff2"), url("/fonts/PlayfairDisplay-BoldItalic.woff") format("woff");
}
@font-face {
  font-family: "Monument Extended";
  font-style: normal;
  font-weight: 800;
  font-display: swap;
  src: url("/fonts/MonumentExtended-Ultrabold.woff2") format("woff2"), url("/fonts/MonumentExtended-Ultrabold.woff") format("woff");
}
*,
::after,
::before {
  box-sizing: border-box;
}

body {
  min-width: 375px;
  min-height: 100vh;
  font-family: "Avenir Next", "Arial", sans-serif;
  color: #2d2a26;
  background-color: #fcfcf0;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  min-width: 100%;
  height: auto;
}

h1,
h2,
h3,
h4 {
  margin: 0;
}

p {
  margin: 0;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: auto;
}

.text-decor {
  font-family: "Playfair Display";
}
.text-decor--blue {
  color: #307fe2;
}
.text-decor--red {
  color: #f87c56;
}
.text-decor--salmon {
  color: #f5cabf;
}
.text-decor--sky {
  color: #8abadd;
}

.overlay {
  z-index: 999;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(51, 51, 51, 0.4);
  opacity: 0;
  transition: all 0.2s ease;
}

.no-scroll {
  overflow: hidden;
}

.btn {
  z-index: 10;
  position: relative;
  top: 0;
  left: 0;
  display: inline-block;
  width: max-content;
  padding: 15px;
  color: #2d2a26;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  background-color: #fcfcf0;
  border: 1px solid #2d2a26;
  border-radius: 10px;
  outline: none;
  cursor: pointer;
  transition: all 0.2s linear;
}
.btn--small {
  padding: 16px 27px;
  font-family: "Avenir Next";
  font-size: 14px;
  font-weight: 600;
  line-height: 19px;
}
.btn--big {
  padding: 15px 36px;
  font-family: "Avenir Next";
  font-size: 16px;
  font-weight: 600;
  line-height: 22px;
}
.btn--sky {
  background-color: #8abadd;
}
.btn--salmon {
  background-color: #f5cabf;
}
.btn--pinky {
  background-color: #ecb3cb;
}
.btn--shadow-black {
  box-shadow: 4px 5px #2d2a26;
}
.btn--shadow-black:hover, .btn--shadow-black:focus {
  top: 2px;
  left: 2px;
  box-shadow: 2px 3px #2d2a26;
}
.btn--shadow-black:active {
  top: 5px;
  left: 4px;
  box-shadow: 0px 0px #2d2a26;
}
.btn--shadow-white {
  box-shadow: 4px 5px #fcfcf0;
}
.btn--shadow-white:hover, .btn--shadow-white:focus {
  top: 2px;
  left: 2px;
  box-shadow: 2px 3px #fcfcf0;
}
.btn--shadow-white:active {
  top: 5px;
  left: 4px;
  box-shadow: 0px 0px #fcfcf0;
}

.header {
  z-index: 100;
  position: sticky;
  top: 0;
  width: 100%;
  background-color: #fcfcf0;
  border-bottom: 1px solid #2d2a26;
}

.header__menu {
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
  height: 82px;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 1200px) {
  .header__menu {
    max-width: 1200px;
  }
}

.header__menu-btn {
  position: relative;
  padding: 0;
  width: 54px;
  height: 54px;
  background-color: #f3f3e7;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  outline: 0;
  transition: all 0.2s ease;
}
.header__menu-btn:hover, .header__menu-btn:focus {
  background-color: #ebebd6;
}

.header__menu-btn > span,
.header__menu-btn > span::before,
.header__menu-btn > span::after {
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  height: 2px;
  width: 26px;
  background-color: black;
  border-radius: 10px;
  transform: translateX(-50%) translateY(-50%);
}

.header__menu-btn > span::before {
  content: "";
  margin-top: -6px;
}

.header__menu-btn > span::after {
  content: "";
  margin-top: 6px;
}

.header__mobile {
  z-index: 1000;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 300px;
  height: min-content;
  padding: 60px 25px 30px;
  background-color: #fcfcf0;
  border: 1px solid #2d2a26;
  border-radius: 15px;
  opacity: 0;
  transition: all 0.2s ease-in;
}

.navigation--desktop {
  display: none;
}

.navigation--mobile .navigation__item {
  font-size: 20px;
  font-weight: 700;
  line-height: 20px;
}

.navigation__list {
  display: flex;
  gap: 25px;
}
.navigation__list--desktop {
  flex-direction: row;
}
.navigation__list--mobile {
  flex-direction: column;
}

.navigation__item {
  font-size: 14px;
  font-weight: 600;
  line-height: 19px;
  transition: all 0.3s ease-out;
}
.navigation__item:hover, .navigation__item:focus {
  transform: translateY(-4px);
}

.navigation__link {
  padding: 10px 0;
}

.navigation__close {
  position: absolute;
  top: 16px;
  right: 14px;
  width: 22px;
  height: 22px;
  background-color: transparent;
  border: 0;
  cursor: pointer;
}

.navigation__close::before,
.navigation__close::after {
  content: "";
  position: absolute;
  top: 10px;
  left: 2px;
  width: 19px;
  height: 2px;
  background-color: #2d2a26;
}

.navigation__close::before {
  transform: rotate(45deg);
}

.navigation__close::after {
  transform: rotate(-45deg);
}

.user-actions {
  display: flex;
  gap: 15px;
}

.user-actions__item--user {
  display: none;
}

.user-actions__link--box {
  padding-right: 58px;
  background-image: url("../img/emoji-box.png");
  background-repeat: no-repeat;
  background-size: 20px 21px;
  background-position: 95px center;
}
.user-actions__link--user {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 15px;
}

.top-bar {
  width: 100%;
  height: 38px;
  background: linear-gradient(90deg, #274cd6 0%, #cf63c6 49.91%, #80d7c4 100%);
  overflow: hidden;
}

.top-bar__ticker {
  display: flex;
  width: 100%;
  height: 100%;
  background-image: url("../img/ticker.svg");
  background-size: auto 13px;
  background-position: 0 center;
  background-repeat: repeat-x;
  animation: ticker 6s linear infinite;
}

@media (min-width: 1200px) {
  .navigation--desktop {
    display: block;
  }
  .user-actions__item--user {
    display: block;
  }
  .header__menu-btn {
    display: none;
  }
}
@keyframes ticker {
  from {
    background-position: 0 center;
  }
  to {
    background-position: -209px center;
  }
}
.promo {
  background-image: linear-gradient(270deg, #cfcdce 0%, #dadadc 44.29%, #dedde2 100%);
}

.promo__wrapper {
  display: grid;
  grid-template-rows: 350px 400px;
}

.promo__content {
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  padding-top: 80px;
  text-align: center;
}
@media (min-width: 1200px) {
  .promo__content {
    max-width: 1200px;
  }
}

.promo__title {
  max-width: 460px;
  font-size: 44px;
  font-weight: 700;
  line-height: 42px;
  letter-spacing: -3px;
}

.promo__image {
  background-image: url("../img/promo-mobile.jpg");
  background-position: center bottom;
  background-repeat: no-repeat;
  background-size: 375px auto;
}

@media (min-width: 1200px) {
  .promo {
    background: linear-gradient(180deg, #dedde2 0%, #e3e6ed 44.29%, #dde6ef 100%);
  }
  .promo__wrapper {
    grid-template-rows: 1fr;
    grid-template-columns: 1fr 1fr;
  }
  .promo__content {
    margin: 0;
    padding-left: 0;
    padding-right: 0;
    gap: 40px;
    padding-top: 90px;
    padding-bottom: 130px;
    padding-left: calc((100vw - 1200px) / 2 + 20px);
    align-items: flex-start;
    text-align: left;
  }
}
@media (min-width: 1200px) and (min-width: 1200px) {
  .promo__content {
    max-width: auto;
  }
}
@media (min-width: 1200px) {
  .promo__title {
    max-width: 520px;
    font-size: 68px;
    font-weight: 700;
    line-height: 65px;
  }
}
@media (min-width: 1200px) {
  .promo__image {
    height: 100%;
    background-image: url("../img/promo-desktop.jpg");
    background-position: left bottom;
    background-size: auto 100%;
  }
}
.insurance {
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 45px;
}
@media (min-width: 1200px) {
  .insurance {
    max-width: 1200px;
  }
}

.insurance__list {
  display: flex;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  width: 100%;
}

.insurance__item {
  width: 160px;
  height: 55px;
}

.insurance__link {
  margin-top: 15px;
  flex-basis: 100%;
  text-align: center;
}

@media (min-width: 1200px) {
  .insurance {
    flex-direction: row;
    flex-wrap: nowrap;
    padding-top: 30px;
    padding-bottom: 35px;
  }
  .insurance__list {
    justify-content: space-between;
    gap: 25px;
  }
  .insurance__item {
    width: 188px;
    height: 65px;
  }
  .insurance__link {
    margin-top: 0;
    flex-basis: auto;
  }
}
.about {
  color: #fcfcf0;
  background-color: #2d2a26;
  border-bottom: 1px solid #2d2a26;
}

.about__wrapper {
  padding-top: 30px;
}

.about__content {
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
  flex-direction: column;
  gap: 25px;
  margin: 0;
}
@media (min-width: 1200px) {
  .about__content {
    max-width: 1200px;
  }
}

.about__title {
  font-size: 36px;
  font-weight: 700;
  line-height: 38px;
  color: white;
  letter-spacing: -3px;
}

.about__text {
  font-size: 18px;
  font-weight: 500;
  line-height: 27px;
  letter-spacing: -0.01em;
}

.about__image {
  min-height: 375px;
  background-image: url("../img/about-image.svg");
  background-position: center bottom;
  background-repeat: no-repeat;
  background-size: auto 100%;
}

@media (min-width: 1200px) {
  .about__wrapper {
    margin: 0 auto;
    padding-left: 20px;
    padding-right: 20px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-top: 65px;
  }
}
@media (min-width: 1200px) and (min-width: 1200px) {
  .about__wrapper {
    max-width: 1200px;
  }
}
@media (min-width: 1200px) {
  .about__wrapper > :last-child {
    order: -1;
  }
}
@media (min-width: 1200px) {
  .about__content {
    margin: 0;
    padding-left: 0;
    padding-right: 0;
    max-width: 520px;
    padding-top: 40px;
    padding-bottom: 90px;
  }
}
@media (min-width: 1200px) and (min-width: 1200px) {
  .about__content {
    max-width: auto;
  }
}
@media (min-width: 1200px) {
  .about__title {
    font-size: 48px;
    font-weight: 700;
    line-height: 50px;
  }
}
@media (min-width: 1200px) {
  .about__link {
    margin-top: 10px;
  }
}
@media (min-width: 1200px) {
  .about__image {
    background-position: left bottom;
  }
}
.features {
  height: calc(100vh - 82px);
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  scroll-snap-type: y mandatory;
}

.features__item {
  position: sticky;
  position: -webkit-sticky;
  top: 0;
  color: #fcfcf0;
  background-image: url("../img/features-bg.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-bottom: 1px solid #2d2a26;
  scroll-snap-align: start;
  scroll-snap-stop: always;
}
.features__item--one .features__image {
  background-image: url("../img/feature-one-desktop.png");
}
.features__item--two .features__image {
  background-image: url("../img/feature-two-desktop.png");
  background-position: center bottom;
}
.features__item--three .features__image {
  background-image: url("../img/feature-three-desktop.png");
}

.features__wrapper {
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
  display: grid;
  grid-template-rows: auto 1fr;
  min-height: calc(100vh - 82px);
}
@media (min-width: 1200px) {
  .features__wrapper {
    max-width: 1200px;
  }
}

.features__content {
  display: flex;
  flex-direction: column;
  gap: 25px;
  margin: 0;
  padding-top: 100px;
}

.features__title {
  font-size: 36px;
  font-weight: 700;
  line-height: 38px;
  letter-spacing: -3px;
}

.features__desc {
  font-size: 18px;
  font-weight: 500;
  line-height: 29px;
}

.features__image {
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

.features__pagination {
  position: absolute;
  top: 0;
  left: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 160px;
  height: 55px;
  color: #2d2a26;
  font-size: 16px;
  font-weight: 600;
  line-height: 15px;
  background-image: url("../img/feature-badge.svg");
  background-size: contain;
  background-repeat: no-repeat;
  transform: translateX(-50%);
}

@media (min-width: 1200px) {
  .features__item--two .features__content {
    margin-left: 100px;
  }
  .features__item--two .features__image {
    order: -1;
  }
  .features__wrapper {
    margin: 0 auto;
    padding-left: 20px;
    padding-right: 20px;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 1200px) and (min-width: 1200px) {
  .features__wrapper {
    max-width: 1200px;
  }
